.footer {
  background-color: rgba(155, 181, 55, .3);
}

.footer-container {
  padding-top: 40px;
  padding-bottom: 40px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .footer-container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .footer-container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.footer-site-link {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.footer-logo {
  letter-spacing: -.04rem;
  justify-content: center;
  margin-right: 0;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.125rem;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer-logo {
    letter-spacing: -.045rem;
    line-height: 1.25rem;
  }
}

.footer-logo-span, .current.footer-link {
  color: #9bb537;
}

.footer-social-list {
  gap: 14px;
  display: flex;
}

.footer-social-item {
  border: 1px solid #000;
  border-radius: 10px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.footer-social-link {
  width: 40px;
  height: 40px;
  display: flex;
}

.footer-social-item:nth-child(1):hover {
  background-color: #3d5b99;
}

.footer-social-item:nth-child(2):hover {
  background-color: #a1755c;
}

.footer-social-item:nth-child(3):hover {
  background-color: #e64a41;
}

.footer-social-icon {
  width: 24px;
  height: 24px;
  fill: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.backdrop {
  opacity: 0;
  visibility: hidden;
  z-index: 1222;
  width: 100%;
  height: 100%;
  background-color: rgba(30, 40, 39, .5);
  padding-top: 20px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
}

.modal-recipe-card {
  max-width: 250px;
  border-radius: 15px;
  padding: 40px 20px;
  position: absolute;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%);
}

@media screen and (min-width: 320px) {
  .modal-recipe-card {
    min-width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .modal-recipe-card {
    min-width: 531px;
    padding: 32px;
  }
}

.modal-btn-close {
  stroke: #050505;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-button-icon {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-btn-close:hover .modal-button-icon {
  transform: scale(1.2);
}

.modal-recipe-card {
  background-color: #f8f8f8;
}

.card-markup-modal {
  color: #050505;
  font-size: 14px;
  line-height: 1.28;
  position: relative;
}

.modal-recipe-name {
  text-transform: uppercase;
  margin-bottom: 14px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.33;
}

@media screen and (min-width: 768px) {
  .modal-recipe-name {
    margin-bottom: 0;
    line-height: 1.22;
    position: absolute;
    top: 0;
  }
}

.iframe-video {
  width: 295px;
  height: 295px;
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .iframe-video {
    width: 467px;
    height: 250px;
    margin-top: 30px;
    margin-bottom: 16px;
  }
}

.modal-general-inf {
  flex-direction: column;
  display: flex;
}

.hashtag-btn-list-tablet {
  display: flex;
}

.card-star-modal {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 33px;
  font-weight: 500;
  display: flex;
}

@media screen and (min-width: 768px) {
  .card-star-modal {
    margin-bottom: 38px;
  }
}

.modal-raiting {
  margin-right: 12px;
}

.starts-modal {
  margin-right: 16px;
  display: flex;
}

.modal-ingr-list {
  margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .modal-ingr-list {
    margin-bottom: 32px;
  }
}

.modal-card-ingr {
  border-bottom: 1px solid rgba(5, 5, 5, .1);
  justify-content: space-between;
  padding-top: 14px;
  padding-bottom: 6px;
  display: flex;
}

.modal-card-measure {
  color: rgba(5, 5, 5, .5);
}

.hashtag-btn-list-tablet {
  gap: 8px;
  margin-bottom: 24px;
}

@media screen and (min-width: 768px) {
  .hashtag-btn-list-tablet {
    margin-bottom: 0;
    position: absolute;
    top: 325px;
  }
}

.hashtag-btn-item {
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 35px;
  padding: 8px 16px;
}

.modal-recipe-instructions {
  color: rgba(5, 5, 5, .5);
}

.extra-modal-button {
  gap: 14px;
  margin-top: 40px;
  font-family: Inter;
  font-weight: 500;
  display: flex;
}

@media screen and (min-width: 768px) {
  .extra-modal-button {
    gap: 16px;
    margin-top: 32px;
  }
}

.modal-add-favorite, .modal-give-rating {
  border-radius: 15px;
  padding: 12px 24px;
}

@media screen and (min-width: 768px) {
  .modal-add-favorite, .modal-give-rating {
    padding: 14px 28px;
  }
}

.modal-add-favorite {
  color: #f8f8f8;
  background-color: #9bb537;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-add-favorite:hover {
  color: #050505;
}

.modal-give-rating {
  border: 1px solid #9bb537;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-give-rating:hover {
  color: #f8f8f8;
  background-color: #9bb537;
}

body.dark-theme .modal-recipe-card {
  background-color: #161616;
}

body.dark-theme .card-markup-modal {
  color: #fff;
}

body.dark-theme .modal-btn-close {
  stroke: #fff;
}

body.dark-theme .modal-raiting {
  color: rgba(255, 255, 255, .5);
